.wrapper {
      display: flex;
      align-items: center;
}
.leftContent {
      flex: 1;
}
.heading {
      & .link {
            text-decoration: none;
      }
      & .link:first-child .link-arrow {
            display: none;
      }
      & .link .link-arrow {
            font-weight: 700;
            color: var(--color-gray-300);
            margin: 0px 10px;
      }
      & .link .link-title {
            font-size: 20px;
            font-weight: 700;
            color: var(--color-gray-300);
      }
      & .link.active .link-title {
            color: var(--color-orange-500);
      }
}
.rightContent {
      height: 80px;
      width: 400px;
      background-color: transparent;
      display: flex;
      justify-content: center;
      align-items: center;
      border-top-left-radius: 10px;
}

.rightContent.dashboard {
      background-color: white;
}
.logo {
      width: 32px;
      height: 32px;
      background-color: var(--color-orange-50);
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      cursor: pointer;
}
.logo.active {
      background-color: var(--color-orange-500);

      & svg path {
            fill: white;
      }
}
.info-user {
      display: flex;
      align-items: center;
      margin-left: 20px;
      cursor: pointer;
}
.info-user img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
}
.info-user-name {
      margin-left: 10px;
      & p:nth-child(1) {
            font-size: 12px;
            font-weight: 400;
            color: var(--color-gray-300);
      }
      & p:nth-child(2) {
            font-size: 16px;
            font-weight: 700;
            color: var(--color-gray-400);
      }
}

.notification {
      position: relative;
      left: 2px;
      top: -18px;
      width: 360px;
      background-color: white;
      height: 526px;
      border-radius: 10px;
      box-shadow: 2px 2px 15px rgba(70, 64, 67, 0.1);
}
.header {
      position: fixed;
      width: 100%;
      font-size: 18px;
      font-weight: 700;
      color: #fff;
      height: 45px;
      line-height: 45px;
      padding-left: 20px;
      background-color: var(--color-orange-400);
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      z-index: 1;
}
.list-notification {
      padding-top: 40px;
      overflow: hidden;
      height: 526px;
      overflow: overlay;
}
